import axios from 'axios'
export default {
  name: 'list',
  data () {
    return {
      userName: '',
      levelChoose: false,
      levelChange: Number,
      list: [],
      level: [
        {
          value: 6,
          label: '最高负责人'
        },
        {
          value: 4,
          label: '老师'
        },
        {
          value: 5,
          label: '助理'
        },
        {
          value: 3,
          label: '学生'
        },
        {
          value: 2,
          label: '专家教授'
        }
      ],
      tableData: [{
        level: 5,
        name: '王小虎',
        id: 1
      }, {
        level: 4,
        name: '王小虎',
        id: 2
      }, {
        level: 3,
        name: '王小虎',
        id: 3
      }, {
        level: 2,
        name: '王小虎',
        id: 4
      }]
    }
  },
  methods: {
    getUserList () {
      axios.get(this.url + '/user/all/')
        .then(this.getUserListSucc)
    },
    getUserListSucc (res) {
      if (res.status === 200) {
        const data = res.data
        this.list = data
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.levelChoose = true
      this.levelChange = row.level
      this.userName = row.name
    },
    handleDelete (index, row) {
      alert(row.id)
      this.$axios.delete(this.url + '/user/id/' + row.id)
    }
  },
  mounted () {
    this.getUserList()
  }
}



